}/*SCROLL MOUSE*/.prueba_scroll {
  position: absolute;
  bottom: 5%;
  left: 50%;
}

.prueba_share {
  position: relative;
}

.prueba_scroll a {
  text-decoration: none;
}

#mouse-scroll {
  style: block;
}

#mouse-scroll {
  -webkit-transform: translateX(-50%);
  z-index: 9999;
}

#mouse-scroll .mouse {
  height: 23px;
  width: 16px;
  border-radius: 10px;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  border: 2px solid red;
  top: 170px;
}

#mouse-scroll .mouse-in {
  height: 7px;
  width: 2px;
  display: block;
  margin: 5px auto;
  background: red;
  position: relative;
}

#mouse-scroll .mouse-in {
  -webkit-animation: animated-mouse 1.2s ease infinite;
  -moz-animation: mouse-animated 1.2s ease infinite;
